@import url(https://fonts.googleapis.com/css?family=Lato:400,700);

body
{
  font-family: 'Lato', 'sans-serif';
}
.profile
{
  min-height: 355px;
  display: inline-block;
}
figcaption.ratings
{
  margin-top: 20px;
}
figcaption.ratings a
{
  color: #f1c40f;
  font-size: 11px;
}
figcaption.ratings a:hover
{
  color: #f39c12;
  text-decoration: none;
}
.divider
{
  border-top: 1px solid rgba(0,0,0,0.1);
}
.emphasis
{
  border-top: 4px solid transparent;
}
.emphasis:hover
{
  border-top: 4px solid #1abc9c;
}
.emphasis h2
{
  margin-bottom: 0;
}
span.tags
{
  background: #1abc9c;
  border-radius: 2px;
  color: #f5f5f5;
  font-weight: bold;
  padding: 2px 4px;
}
#sect
{
  border-bottom: 2px solid;
  display: relative;
  padding-bottom: 20px;
}
.profile
{
  
}
.profile p
{
  font-size: 1.3em;
}



